"use client";

import React, { useState } from "react";
import { Shield, Bell, Eye, EyeOff, EyeIcon } from "lucide-react";
import { BellIcon, EyeOffIcon, SecurityIcon } from "@/assets/svg/svg_icon";

const SettingsPage = () => {
  const [activeTab, setActiveTab] = useState<"security" | "notification">(
    "security",
  );
  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [showCurrentPassword, setShowCurrentPassword] = useState(false);
  const [showNewPassword, setShowNewPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);

  const handleChangePassword = () => {
    // Handle password change logic here
    console.log("Changing password...");
  };

  return (
    <div className="border border-gray-200 rounded-[20px] bg-white">
      {/* Page Title */}
      <h1 className="text-2xl font-semibold text-gray-900 p-5 border-b border-gray-200">
        Settings
      </h1>

      <div className="grid grid-cols-1 lg:grid-cols-4 gap-6 mt-5 mb-5 p-5">
        {/* Sidebar */}
        <div className="lg:col-span-1">
          <div className="bg-white border border-gray-200 rounded-[20px] p-2 space-y-2">
            <button
              onClick={() => setActiveTab("security")}
              className={`w-full flex items-center gap-3 px-3 py-3 rounded-lg text-left transition-colors cursor-pointer ${
                activeTab === "security"
                  ? "text-[#003625]"
                  : "text-gray-600 hover:text-[#003625]"
              }`}
            >
              {activeTab === "security" ? (
                <SecurityIcon stroke="#003625" />
              ) : (
                <SecurityIcon stroke="#0C0F16" />
              )}
              <span className="font-medium">Security settings</span>
            </button>
            <button
              onClick={() => setActiveTab("notification")}
              className={`w-full flex items-center gap-3 px-3 py-3 rounded-lg text-left transition-colors cursor-pointer ${
                activeTab === "notification"
                  ? "text-[#003625]"
                  : "text-gray-600 hover:text-[#003625]"
              }`}
            >
              {activeTab === "notification" ? (
                <BellIcon stroke="#003625" />
              ) : (
                <BellIcon stroke="#0C0F16" />
              )}
              <span className="font-medium">Notification settings</span>
            </button>
          </div>
        </div>

        {/* Main Content */}
        <div className="lg:col-span-3">
          <div className="bg-white border border-gray-200 rounded-[20px] p-5">
            {activeTab === "security" && (
              <>
                <h2 className="text-xl font-semibold text-gray-900 mb-6">
                  Security Settings
                </h2>

                {/* Change Password Section */}
                <div>
                  <h3 className="font-semibold text-gray-900 mb-2">
                    Change password
                  </h3>
                  <p className="text-gray-600 mb-6">
                    To change your password, kindly enter your current password
                    and the new password you want to change to.
                  </p>

                  <div className="space-y-4">
                    {/* Current Password */}
                    <div>
                      <label className="block text-sm text-gray-700 mb-2">
                        Current password
                      </label>
                      <div className="flex items-center border border-gray-300 rounded-[16px] px-4 py-3 w-full max-w-[400px]">
                        <input
                          type={showCurrentPassword ? "text" : "password"}
                          value={currentPassword}
                          onChange={(e) => setCurrentPassword(e.target.value)}
                          placeholder="Enter your current password"
                          className="flex-1 outline-none border-none"
                        />

                        <button
                          type="button"
                          onClick={() =>
                            setShowCurrentPassword(!showCurrentPassword)
                          }
                          className="text-gray-400 hover:text-gray-600"
                        >
                          {showCurrentPassword ? <EyeOffIcon /> : <EyeIcon />}
                        </button>
                      </div>
                    </div>

                    {/* New Password and Confirm Password */}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      {/* New Password */}
                      <div className="w-full">
                        <label className="block text-sm text-gray-700 mb-2">
                          New password
                        </label>
                        <div className="flex items-center border border-gray-300 rounded-[16px] px-4 py-3 w-full gap-2">
                          <input
                            type={showNewPassword ? "text" : "password"}
                            value={newPassword}
                            onChange={(e) => setNewPassword(e.target.value)}
                            placeholder="Enter a new password"
                            className="flex-1 outline-none border-none"
                          />
                          <button
                            type="button"
                            onClick={() => setShowNewPassword(!showNewPassword)}
                            className="text-gray-400 hover:text-gray-600"
                          >
                            {showNewPassword ? <EyeOffIcon /> : <EyeIcon />}
                          </button>
                        </div>
                      </div>

                      {/* Confirm New Password */}
                      <div className="w-full">
                        <label className="block text-sm text-gray-700 mb-2">
                          Confirm new password
                        </label>
                        <div className="flex items-center border border-gray-300 rounded-[16px] px-4 py-3 w-full gap-2">
                          <input
                            type={showConfirmPassword ? "text" : "password"}
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                            placeholder="Enter your new password again"
                            className="flex-1 outline-none border-none"
                          />
                          <button
                            type="button"
                            onClick={() =>
                              setShowConfirmPassword(!showConfirmPassword)
                            }
                            className="text-gray-400 hover:text-gray-600"
                          >
                            {showConfirmPassword ? <EyeOffIcon /> : <EyeIcon />}
                          </button>
                        </div>
                      </div>
                    </div>

                    {/* Change Password Button */}
                    <div className="pt-2">
                      <button
                        type="button"
                        className="group w-[200px] p-0.5 border border-[#003625] rounded-[18px] cursor-pointer disabled:opacity-50 disabled:cursor-not-allowed"
                      >
                        <div className="w-full bg-[#003625] text-white py-3 rounded-[14px] text-center font-semibold transition-colors duration-200 group-hover:bg-emerald-100 group-hover:text-[#052214]">
                          Change Password
                        </div>
                      </button>
                    </div>
                  </div>
                </div>
              </>
            )}

            {activeTab === "notification" && (
              <div>
                <h2 className="text-xl font-semibold text-gray-900 mb-6">
                  Notification Settings
                </h2>
                <p className="text-gray-600">
                  Notification settings content goes here...
                </p>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default SettingsPage;
